.loader {
  perspective: 1000px;
  width: 280px;
  height: 280px;
  color: silver;
  border-radius: 50%;
  margin: 10px auto 0;
  transform: rotateZ(45deg);
}

.loader:before, .loader:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateX(70deg);
}

.loader:after {
  color: #ff3d00;
  animation-delay: .4s;
  transform: rotateY(70deg);
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%)rotateZ(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotateZ(360deg);
  }
}

@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotate(-360deg);
  }
}

@keyframes spin {
  0%, 100% {
    box-shadow: .8em 0;
  }

  12% {
    box-shadow: .8em .8em;
  }

  25% {
    box-shadow: 0 .8em;
  }

  37% {
    box-shadow: -.8em .8em;
  }

  50% {
    box-shadow: -.8em 0;
  }

  62% {
    box-shadow: -.8em -.8em;
  }

  75% {
    box-shadow: 0 -.8em;
  }

  87% {
    box-shadow: .8em -.8em;
  }
}

